const formRule = {
  //0
  validateNumber(rule, value, callback) {
    var reg = /^\d+(\.\d{1,2})?$/
    if ((value && !reg.test(value)) || value === 0) {
      return callback(new Error('0()'))
    } else {
      return callback()
    }
  },
  //0-100()
  validatePercent(rule, value, callback) {
    let reg = /^(\d|[1-9]\d)(\.\d{1,2})?$/
    if (value && !reg.test(value)) {
      if (Number(value) === 100) {
        return callback()
      } else {
        return callback(new Error('0-100()'))
      }
    } else {
      return callback()
    }
  },
  //0
  validateInteger(rule, value, callback) {
    let reg = /^\+?[1-9]\d*$/
    if (value && !reg.test(Number(value))) {
      return callback(new Error('0'))
    } else {
      return callback()
    }
  },
  //httphttps
  validateLink(rule, value, callback) {
    let reg = /(http|https):\/\/([\w.]+\/?)\S*/
    if (value && !reg.test(value)) {
      return callback(new Error(''))
    } else {
      return callback()
    }
  },
  //
  validateNoChinese(rule, value, callback) {
    let reg = /[\u4E00-\u9FA5]/g
    if (value && reg.test(value)) {
      return callback(new Error(''))
    } else {
      return callback()
    }
  },
  //
  validateEAN(rule, value, callback) {
    let reg = /^[A-Za-z0-9]+$/
    if (value && !reg.test(value)) {
      return callback(new Error(''))
    } else {
      return callback()
    }
  },
  //
  validateIdcard(rule, value, callback) {
    let reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}$)/;
    if (value && !reg.test(value)) {
      return callback(new Error(''))
    } else {
      return callback()
    }
  },
  //8~16//
  validatePassword(rule, value, callback) {
    let reg = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,16}$/
    if (value && !reg.test(value)) {
      return callback(new Error('8~16/'))
    } else {
      return callback()
    }
  },
  // 
  validateMobile(rule, value, callback) {
    let mobile = /^1[3|4|5|6|7|8|9][0-9]{9}$/g
    if (value && !mobile.test(value)) {
      return callback(new Error(''))
    } else {
      return callback()
    }
  },
  // 
  validatePhone(rule, value, callback) {
    let mobile = /^1[3|4|5|6|7|8|9][0-9]{9}$/g
    let phone = /^((\d{3,4})-)(\d{6,8})(-(\d{3,}))?$/
    if (value && (!(mobile.test(value) || phone.test(value)))) {
      return callback(new Error(''))
    } else {
      return callback()
    }
  },
  // 
  validateEmail(rule, value, callback) {
    let email = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/
    if (value) {
      if (email.test(value)) {
        return callback()
      } else {
        return callback(new Error(''))
      }
    } else {
      return callback()
    }
  },
  // 
  validateVersionNo(rule, value, callback) {
    let reg = /^(\w{1})(\w|\.|-|\s*)+$/
    if (value !== undefined) {
      if (reg.test(value)) {
        return callback()
      } else {
        return callback(new Error(''))
      }
    }
  }
}

export default formRule